import React, { useMemo, useState } from "react";
import { AutoComplete, Input } from "antd";
import styles from "./styles.module.scss";
import { useNavigate } from "react-router-dom";
import Highlighter from "react-highlight-words";

export const Header = ({ onSearch, onSelect, iphones = [] }) => {
  const [searchValue, setSearchValue] = useState("");
  const navigate = useNavigate();

  const options = useMemo(() => {
    return iphones
      .filter((iphone) =>
        iphone.title.toLowerCase().includes(searchValue.toLowerCase())
      )
      .map((iphone) => ({
        label: (
          <Highlighter
            searchWords={[searchValue]}
            textToHighlight={iphone.title}
          />
        ),
        value: JSON.stringify(iphone),
      }));
  }, [iphones, searchValue]);

  const handleSelect = (value) => {
    const iphone = JSON.parse(value);
    setSearchValue(iphone.title);
    onSelect(iphone);
  };

  return (
    <div className={styles["header"]}>
      <h2 className={styles["logo"]} onClick={() => navigate("/")}>
        TezFind
      </h2>
      <AutoComplete
        popupClassName="certain-category-search-dropdown"
        onSelect={handleSelect}
        options={options}
        value={searchValue}
      >
        <Input.Search
          value={searchValue}
          onChange={(e) => setSearchValue(e.target.value)}
          placeholder="  "
          allowClear
          enterButton=""
          size="large"
          onSearch={onSearch}
        />
      </AutoComplete>
    </div>
  );
};
