import { Layout } from "antd";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import styles from "./styles.module.scss";
import { TypeCard, Header, Footer } from "../../components/modules";
import { SimpleSpinner } from "../../components/ui";
import { getAllIphones, getIphoneTypes } from "../../store";

export const Main = () => {
  const [iphoneTypes, setIphoneTypes] = useState([]);
  const [allIphones, setAllIphones] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    getTypes();
    getIphones();
  }, []);

  const getTypes = async () => {
    const response = await getIphoneTypes();
    setIphoneTypes(response);
  };

  const getIphones = async () => {
    const response = await getAllIphones();
    setAllIphones(response);
  };

  const handleSearch = (value) => {
    console.log("on search", value);
  };

  const handleSelect = (iphone) => {
    console.log("on select", iphone);
  };

  return (
    <Layout>
      <Header
        onSearch={handleSearch}
        onSelect={handleSelect}
        iphones={allIphones}
      />

      <div className={styles["all-cards"]}>
        <div className={styles["cards"]}>
          {!iphoneTypes.length ? (
            <SimpleSpinner />
          ) : (
            iphoneTypes.map(({ typeKey, type, image }) => (
              <TypeCard
                onClick={() => navigate(typeKey)}
                key={type}
                title={type}
                image={image}
                defaultImage="https://asiastore.kg/image/cache/catalog/iphone/iphone14/iphone14/purple/wwen_iphone14_q422_purple_pdp_image_position-1a-670x540.jpg"
              />
            ))
          )}
        </div>
      </div>

      <Footer />
    </Layout>
  );
};
